@import '../../styles/palette.scss';

.leagues {
    background-color: $byzantine;
    padding: 1rem 1.25rem;
    z-index: 1;
    position: relative;
    top: 5rem;
    
    > div > span {
        width: 8rem;
    }

    .league-name {
        padding: 0.25rem 1.25rem;
    }

    .MuiFormControl-root {
        width: 100%;
    }

    .afo-radio {
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
}